@font-face {
  font-family: 'Framework7 Icons';
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Framework7Icons-Regular.eot");
  src: local('Framework7 Icons'),
  local('Framework7Icons-Regular'),
  url("../fonts/Framework7Icons-Regular.woff2") format("woff2"),
  url("../fonts/Framework7Icons-Regular.woff") format("woff"),
  url("../fonts/Framework7Icons-Regular.ttf") format("truetype");
}

.f7-icons {
  font-family: 'Framework7 Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 25px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: "liga";
  -moz-font-feature-settings: "liga=1";
  -moz-font-feature-settings: "liga";
  font-feature-settings: "liga";
}

.preloader-indicator-modal {
  background: transparent;
}

.views {
  //padding-bottom: 50px;
}

.footer {
  padding: 15px 0;
  border-top: 1px solid #e0e0e0;
  background-color: #f7f7f7;

  .navy-line {
    width: 180px;
    height: 1px;
    margin: 15px auto 5px;
    border-bottom: 1px solid #e0e0e0;
  }
}

.footer-item {
  text-align: center;
}

.footer-item__title {
  padding-bottom: 8px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
}

.footer-item__cell-social a {
  padding: 5px;
  color: #828282;
  font-size: 24px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.footer-copyright {
  padding-top: 5px;
  color: #9d9d9d;
  font-size: 13px;
  text-align: center;
  p {
    padding-bottom: 0;
    margin: 0;
  }
  a {
    color: #9d9d9d;
    border-bottom: 1px solid rgba(174, 174, 174, 0.3);
  }
}

.index-footer {
  position: absolute;
  //bottom: 0;
}

.list-block-search {
  margin-top: -17px;
}

#searchForm {
  .time-lease {
    margin-bottom: 0;
    background: #fff;
    .button {
      padding: 5px 0;
      border-radius: 0;
      border-width: 0;
      border-color: @border-color-base;
      height: 42px;
      line-height: 32px;
      font-size: 17px;
      color: @text-color-base;
      span {
        position: relative;
        display: block;
        height: 100%;
        &:before {
          content: '';
          position: absolute;
          left: 15px;
          bottom: -5px;
          right: 15px;
          height: 1px;
          //width: 100%;
          background-color: transparent;
          display: block;
          z-index: 15;
          -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;

        }
      }
      &:first-child {
        span {
          border-right: 1px solid @border-color-base;
        }
      }
      input {
        position: absolute;
        clip: rect(0, 0, 0, 0);
        pointer-events: none;

        &:checked {
          + span {
            color: @green;
            font-weight: 400;
            &:before {
              background-color: @green;
            }
          }
        }
      }
    }
  }
}

.list-block {
  ul {
    &:before,
    &:after {
      background-color: @border-color-base;
    }
  }

  .item-content {
    &.item-header {
      min-height: 36px;
      .item-inner {
        min-height: 36px;
        padding-bottom: 0;
      }
    }
    .item-inner {

      &.inner-not-border {
        &:after {
          background-color: transparent;
        }
      }
      &.input-group {
        .item-input {
          position: relative;
          //border-right: 1px solid @border-color-base;

          input {
            padding: 0 8px;
          }

          &:first-child {
            &:before {
              content: '';
              position: absolute;
              top: 10px;
              bottom: 10px;
              right: 4px;
              width: 1px;
              background-color: @border-color-base;
              z-index: 15;
            }
          }
        }
      }
      &:after {
        background-color: @border-color-base;
      }
    }
  }
}

.label-switch input[type=checkbox]:checked + .checkbox {
  background: @green;
}

i.icon.mobile-menu {
  width: 20px;
  height: 20px;
  background-image: url(../img/mobile-menu.svg?color=black);
  background-position: 50% 50%;
  background-size: 100% 100%;
}

.panel-left {
  background: #1f1f1f;
  .content-block {
    margin: 25px 0;
    padding: 0;
    color: #fff !important;

    .list-block {
      margin: 25px 0;
      font-size: 16px;

      ul {
        background: #1f1f1f;
        &:before,
        &:after {
          background-color: transparent;
        }

        .divider {
          height: 1px;
          background-color: #fff;
          padding: 0;
        }
      }

      .item-content {
        padding-left: 15px;
        min-height: 44px;

        .f7-icons {
          font-size: 18px;
        }
      }
    }

    .item-link {
      .item-inner {
        background-image: none;
      }
    }
  }
}

/*******************
      Список
*******************/

.realty-list {
  .item-content {
    padding-left: 10px;
  }
  .item-media {
    position: relative;
    //height: 100px;
    //width: 100px;
    //border-radius: 50%;
    img {
      display: block;
      height: 100px;
      width: 100px;
      border-radius: 50%;
    }

    .count-icon {
      position: absolute;
      bottom: 22px;
      left: 50%;
      margin-left: -22px;
      padding: 1px 8px;
      display: inline-block;
      font-weight: 500;
      color: #fff;
      font-size: 14px;
      vertical-align: middle;
      border-radius: 2px;
      background: rgba(0, 0, 0, 0.4) none repeat scroll 0 0;
    }
  }
  .item-inner {
    .item-title {
      font-size: 16px;
    }
    .item-subtitle {
      font-size: 14px;
    }
  }
}

