  background: #fff;
  .date-views {
    color: @grey-base;
    span {
      margin-right: 10px;

      .f7-icons {
        font-size: 16px;
      }
    }
  }
}

.view-title {
  margin-bottom: 6px;
  font-size: 24px;
  color: @black;
  font-weight: 600;
}

.view-address {
  font-size: 16px;
  font-weight: 400;
}

.view-photo {
  + .content-block {
    margin-top: 15px;
  }
}

.view-price {
  padding-top: 15px;
  font-size: 24px;
  font-weight: 600;
  color: @black;
}

.view-to-call {
  padding: 15px 0 5px;
}

.view-contacts {
  padding: 15px 0;

  .fio {
    font-size: 16px;
  }

  .phone {
    padding-bottom: 15px;
    font-size: 24px;
    font-weight: 600;
  }
}

.v-dotted-line {
  position: relative;
  display: block;
  line-height: 1.23em;
}

.v-dotted-line + .v-dotted-line {
  margin-top: 0.7em;
}

.v-dotted-line__left,
.v-dotted-line__right {
  display: inline-block;
  vertical-align: top;
  letter-spacing: normal;
}

.v-dotted-line__left {
  width: 66%;
  margin-right: -66%;
  overflow: hidden;
}

.v-dotted-line__title {
  display: inline;
}

.v-dotted-line__line {
  display: inline-block;
  width: 100%;
  margin: 0 -100% 0 0;
  vertical-align: baseline;
  border-bottom: 1px dotted @border-color-base;
}

.v-dotted-line__right {
  margin-left: 66%;
}

.v-dotted-line__content {
  display: block;
}

.v-line-title {
  padding: 15px 10px 10px 0;
  font-size: 18px;
  font-family: inherit;
  font-weight: 600;
  line-height: 1.1;
  color: @black;
}

.popup-map {
  .popup-map-body {
    position: relative;
    height: 100%;
    width: 100%;
    #map {
      height: 100%;
      width: 100%;
      z-index: 1;
    }
    .map-close {
      width: 40px;
      height: 40px;
      background: #fff url("../img/fullscreen-exit.svg") center no-repeat;
      display: inline-block;
      vertical-align: middle;
      transition: background-color .15s ease-out, border-color .15s ease-out, opacity .15s ease-out;
      cursor: pointer;
      box-sizing: border-box !important;
      box-shadow: 0 1px 2px 1px rgba(0, 0, 0, .15), 0 2px 5px -3px rgba(0, 0, 0, .15);
      border-width: 1px;
      border-style: solid;
      border-radius: 3px;
      border-color: transparent;
      background-clip: border-box;
      color: #000;
      font-family: Arial, Helvetica, sans-serif;
    }
  }
}

.static-map {
  overflow: hidden;
  position: relative;
  margin: 15px -15px;
  z-index: 0;
  .map-img {
    position: relative;
    margin-left: -300px;
    left: 50%
  }
}


/*******************************
           messages
*******************************/

.messages {
  background: #FAFAFB;
  border: 1px solid #e7eaec;
  border-left-width: 3px;
  margin: 0 0 20px;
  padding: 15px;
  border-radius: 2px;

  &:hover,
  &:focus {
    background: #fff;
  }

  .message--title {
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 18px;
    font-weight: 500;
  }

  &.messages--error {
    border-left-color: #a51b00;
    .message--title {
      color: #a51b00;
    }
  }

  &.messages--warning {
    border-left-color: #f8ac59;
    .message--title {
      color: #f8ac59;
    }
  }

  &.messages--success {
    border-left-color: @green;
    .message--title {
      color: @green;
    }
  }

  &.messages--info {
    border-left-color: #1c84c6;
    .message--title {
      color: #1c84c6;
    }
  }

  &.messages--danger {
    border-left-color: #ed5565;
    .message--title {
      color: #ed5565;
    }
  }

}

