/*******************************
           Блог
*******************************/

.blog-article {
  h2 {
    font-size: 20px;
  }
}

.blog-preview-block {
  clear: both;
  height: 400px;
  margin-bottom: 15px;
  margin-top: 0;
  padding: 0;
  border: 1px solid #e7eaec;
  transition: all .2s linear;
  color: @text-color-base;
  &:hover {
    box-shadow: 0 1px 5px 1px rgba(0,0,0,.1);
  }
}

.blog-preview-img {
  height: 200px;
  background-color: #f8f8f9;
  overflow: hidden;
  img {
    height: 100%;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }
}

.blog-preview-content {
  position: relative;
  padding: 15px 20px 20px;
  box-sizing: border-box;
  height: calc(100% - 200px);
  p {
    max-height: 60px;
    margin-top: 0;
    overflow: hidden;
    &.title {
      max-height: 66px;
      color: #262626;
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 12px;
      font-weight: 500;
    }
  }

  .blog-preview-bottom {
    position: absolute;
    bottom: 15px;
    width: calc(100% - 40px);
    height: 20px;

  }
}

.wrapper-blog {
  margin-bottom: 15px;
  .title {
    margin: 0 0 10px;
    font-size: 22px;
    text-transform: inherit;
  }

  h2 {
    font-size: 18px !important;
  }

  .article-date {
    margin-bottom: 15px;
  }
}

.clearfix {
  &:before,
  &:after {
    content: " "; // 1
    display: table; // 2
  }
  &:after {
    clear: both;
  }
}

/***************************
   Модал соц. ссылки
***************************/

ul.social-network {
  list-style: none;
  display: block;
  margin-left:0 !important;
  padding: 15px 0 0;
  text-align: center;
}

.social-circle {
  li {
    display: inline;
    margin: 0 5px;
    opacity: 0.85;
    &:hover {
      opacity: 1;
    }
    a {
      display:inline-block;
      position:relative;
      margin:0 auto;
      border-radius:50%;
      text-align:center;
      width: 42px;
      height: 42px;
      font-size:20px;
      border: 2px solid transparent;
      //background: @green;

      &.icoFacebook {
        border-color: #3B5998;
        color: #3B5998;
      }
      &.icoTwitter {
        border-color: #33ccff;
        color: #33ccff;
      }
      &.icoGoogle {
        border-color: #BD3518;
        color: #BD3518;
      }
      &.icoVk {
        border-color: #527498;
        color: #527498;
      }
      &.icoOk {
        border-color: #ee8208;
        color: #ee8208;
      }
      i {
        margin: 0;
        line-height: 38px;
        text-align: center;
        //color: #fff;
        transition: all 0.8s;
      }

      &:hover i {
        transform: rotate(360deg);
        transition: all 0.2s;
      }
    }
  }
}

.text-social {
  display: inline;
  margin: 0 auto;
}



.adsense {
  position: relative;
  background-color: #fff;
  display: block;
  margin: 0 auto;
  padding: 10px;
  box-sizing: border-box;
  &:after {
    content: '';
    position: absolute;
    left: 125px;
    bottom: 0;
    right: 0;
    top: auto;
    height: 1px;
    background-color: #e2e2e2;
    display: block;
    z-index: 15;
    transform-origin: 50% 100%
  }
}

.ajax-content-load {
  position: relative;
  display: block;
  height: 50px;
  margin-bottom: 10px;
  padding: 15px;
  clear: both;
  .preloader {
    position: absolute;
    left: 50%;
    margin-left: -10px;
  }
}



