@font-face {
  font-family: 'Exo 2';
  src: url(../fonts/exotwo/exo2thin.eot);
  src: url(../fonts/exotwo/exo2thin.eot?#iefix) format('embedded-opentype'),
  url(../fonts/exotwo/exo2thin.woff2) format('woff2'),
  url(../fonts/exotwo/exo2thin.woff) format('woff'),
  url(../fonts/exotwo/exo2thin.ttf) format('truetype');
  font-weight: 100;
  font-style: normal;
}

